.w-prismjs[class*="language-"] {
  font-size: 85%;
  position: relative;
  overflow-x: auto;
  background-color: #f6f8fa;
  color: #000;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  -webkit-hyphens: none;
      -ms-hyphens: none;
          hyphens: none;
}
.w-prismjs[class*="language-"] code {
  background: none;
  overflow-wrap: normal;
  white-space: inherit;
  -moz-tab-size: 2;
       tab-size: 2;
  padding: 16px;
  line-height: 18px;
  display: block;
  text-shadow: 0 1px #fff;
}
.w-prismjs[class*="language-"] code .token.tag .attr-value {
  color: #032f62;
}
.w-prismjs[class*="language-"] code .token.property,
.w-prismjs[class*="language-"] code .token.tag,
.w-prismjs[class*="language-"] code .token.boolean,
.w-prismjs[class*="language-"] code .token.number,
.w-prismjs[class*="language-"] code .token.function-name,
.w-prismjs[class*="language-"] code .token.constant,
.w-prismjs[class*="language-"] code .token.symbol,
.w-prismjs[class*="language-"] code .token.deleted {
  color: #0060c9;
}
.w-prismjs[class*="language-"] code .token.punctuation {
  color: #a0a0a0;
}
.w-prismjs[class*="language-"] code[class*="language-"] {
  color: black;
}
.w-prismjs[class*="language-"] code[class*="language-"] .token.selector,
.w-prismjs[class*="language-"] code[class*="language-"] .token.attr-name,
.w-prismjs[class*="language-"] code[class*="language-"] .token.string,
.w-prismjs[class*="language-"] code[class*="language-"] .token.char,
.w-prismjs[class*="language-"] code[class*="language-"] .token.function,
.w-prismjs[class*="language-"] code[class*="language-"] .token.builtin {
  color: #6f42c1;
}
.w-prismjs[class*="language-"] code[class*="language-"] .token.inserted {
  color: #22863a;
  background-color: #f0fff4;
}
.w-prismjs[class*="language-"] code[class*="language-"] .token.deleted {
  color: #b31d28;
  background-color: #ffeef0;
}
.w-prismjs[class*="language-"] code[class*="language-"] .token.class-name {
  color: #6f42c1;
}
.w-prismjs[class*="language-"] code[class*="language-"] .code-block {
  color: #032f62;
}
.w-prismjs[class*="language-"] code[class*="language-"] .token.comment,
.w-prismjs[class*="language-"] code[class*="language-"] .token.block-comment,
.w-prismjs[class*="language-"] code[class*="language-"] .token.prolog,
.w-prismjs[class*="language-"] code[class*="language-"] .token.doctype,
.w-prismjs[class*="language-"] code[class*="language-"] .token.cdata {
  color: #7D8B99;
}
.w-prismjs[class*="language-"] code[class*="language-"] .token.punctuation {
  color: #a0a0a0;
}
.w-prismjs[class*="language-"] code[class*="language-"] .token.operator,
.w-prismjs[class*="language-"] code[class*="language-"] .token.entity,
.w-prismjs[class*="language-"] code[class*="language-"] .token.url,
.w-prismjs[class*="language-"] code[class*="language-"] .token.variable {
  color: #d73a49;
  background: rgba(255, 255, 255, 0.5);
}
.w-prismjs[class*="language-"] code[class*="language-"] .token.atrule,
.w-prismjs[class*="language-"] code[class*="language-"] .token.attr-value {
  color: #004698;
}
.w-prismjs[class*="language-"] code[class*="language-"] .token.keyword {
  color: #d63200;
}
.w-prismjs[class*="language-"] code[class*="language-"] .token.regex,
.w-prismjs[class*="language-"] code[class*="language-"] .token.important {
  color: #e90;
}
.w-prismjs[class*="language-"] code[class*="language-"] .token.string {
  color: #0a53c1;
}
